
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
components: {
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast,
Flexbox,
FlexboxItem
},
computed: {
leftOptions () {
return {
showBack: true,
backText: ''
}
},
rightOptions () {
return {
showMore: false
}
}
},
mounted () {
window.onerror = function (message) {
alert(message)
}
this.failureShowToast = false
this.changeSuccessShow = false
},
data () {
return {
title: 'Update password',
mobile: '',
email: '',
randomCode: '',
newPassword: '',
repeatPassword: '',
iconType: '',
failureText: '',
successText: '',
changeSuccessShow: false,
changeFailureShow: false,
showNoEmail: false,
noEmail: 'Invalid Email',
updateSubmit: 'UPDATE',
signUpLink: '',
forgetPwdLink: '',
LoginDisable: false
}
},
methods: {
getVertifyCode () {
if (this.email) {
this.$http.post(this.$apis.getRandomCode, {email: this.email})
.then((res) => {
this.showNoEmail = true
this.noEmail = res.msg
})
.catch(function (error) {
console.log(error)
})
} else {
this.showNoEmail = true
}
},
goLogin () {
this.$router.push('/')
},
change (val) {
console.log('on change', val)
},
onBlur (val) {
console.log('on blur', val)
},
onFocus (val, $event) {
console.log('on focus', val, $event)
},
onEnter (val) {
console.log('click enter!', val)
},
updatePassword () {
this.$http.post(this.$apis.updatePassword, {
email: this.email,
password: this.newPassword,
randomCode: this.randomCode
}).then(res => {
if (res.code === 200) {
this.changeSuccessShow = true
this.successText = res.msg
} else {
this.changeFailureShow = true
this.failureText = res.msg
}
})
}
}
}
window.onError = function (message) {
alert(message)
}



